/*  the default website starts here     *//*---------------------------------------------------------------------------------------------------------------------------*/
/* to terget everywhere */
html{
    background: red;
    font-size:16px;
    font-family:verdana;
    text-align:justify;
}

body{
    background: whitesmoke;
}

*{
    margin:0 auto;
    line-height:1.5em;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    -ms-box-sizing: border-box;
    -o-box-sizing: border-box;
} 

:disabled{
    opacity:0.3;
    filter:alpha(opasity=30);
    border:1px solid black;
}

table, table th, table td, table tr{
    border:1px solid black;
    border-collapse: collapse;
}


h1{
    font-size:200%;
    line-height: 1.3em;
}

h2{
    font-size:150%;
    line-height: 1.3em;
}

h3{
    font-size:130%;
    line-height: 1.3em;
}

h4{
    font-size:100%;
}

h5{
    font-size:80%;
}

h6{
    font-size:70%;
}

a{
    color:black;
}

a:hover{
    color:blue;
}

.centerH{
    text-align: center;
}

.centerHH{
    text-align: center;
    display:block;
}

.centerVertically{
    position: relative;
    top:50%;
    transform:translate(0%, -50%);
    -ms-transform:translate(0%, -50%);
    -webkit-transform:translate(0%, -50%);
    -moz-transform:translate(0%, -50%);
    -o-transform:translate(0%, -50%);
}

.centerVertiHori{
    position: relative;
    top:50%;
    left:50%;
    transform:translate(-50%, -50%);
    -webkit-transform:translate(-50%, -50%);
    -moz-transform:translate(-50%, -50%);
    -ms-transform:translate(-50%, -50%);
    -o-transform:translate(-50%, -50%);
}

@keyframes logo{
    25%{top:20%; left:20%;}
    50%{top:80%; left:80%;}
    75%{top:30%; left:30%;}
    100%{top:0; left:0;}
}
@-webkit-keyframes logo{
    25%{top:20%; left:20%;}
    50%{top:80%; left:80%;}
    75%{top:30%; left:30%;}
    100%{top:0; left:0;}
}
@-moz-keyframes logo{
    25%{top:20%; left:20%;}
    50%{top:80%; left:80%;}
    75%{top:30%; left:30%;}
    100%{top:0; left:0;}
}
@-ms-keyframes logo{
    25%{top:20%; left:20%;}
    50%{top:80%; left:80%;}
    75%{top:30%; left:30%;}
    100%{top:0; left:0;}
}
@-o-keyframes logo{
    25%{top:20%; left:20%;}
    50%{top:80%; left:80%;}
    75%{top:30%; left:30%;}
    100%{top:0; left:0;}
}

@keyframes form{
    from{opacity:0;}
    to{opacity:1;}
}
@-webkit-keyframes form{
    from{opacity:0;}
    to{opacity:1;}
}
@-moz-keyframes form{
    from{opacity:0;}
    to{opacity:1;}
}
@-ms-keyframes form{
    from{opacity:0;}
    to{opacity:1;}
}
@-o-keyframes form{
    from{opacity:0;}
    to{opacity:1;}
}

@keyframes formInside{
    from{left:200%;}
    to{left:0;}
}
@-webkit-keyframes formInside{
    from{left:200%;}
    to{left:0;}
}
@-moz-keyframes formInside{
    from{left:200%;}
    to{left:0;}
}
@-ms-keyframes formInside{
    from{left:200%;}
    to{left:0;}
}
@-o-keyframes formInside{
    from{left:200%;}
    to{left:0;}
}

@keyframes section_spin_contain {
    0% { opacity:0.3 ; }
    100% { opacity:1; }
}

@keyframes section_spin_loader {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}





/* heading */
header{
    background:whitesmoke;
    width:100%;
    height:70px;
}

.logo{
    height:60;
    width:190px;
    position:relative;
    animation:logo 2s;
    -webkit-animation:logo 2s;
    -moz-animation:logo 2s;
    -ms-animation:logo 2s;
    -o-animation:logo 2s;
}

.header-login-out{
    position: relative;
    top:32%;
    display:none;
}

.header-login-out a{
    text-decoration: none;
    color: #f2e9e1;
    background: rgb(27, 27, 216);
    display:block;
    padding:8px ;
    border-radius: 5px;
}

/* navigation bar */
nav{
    background:blue;
    width:100%;
    height: 50px;
    position:sticky;
    position: -webkit-sticky;/*for Safari*/
    top:0;
    z-index:1;
}

.login-out{
   float:right;
   margin-top:2px;
}

.login-out-in a, .login-out-out a{
    height:45px;
    padding: 9px 5px 0px 5px;
    display:block;
}

.login-out-in a:hover{
    
    background:green ;
    color:white;

}

.login-out-out a:hover{
    background:red ;
    color:white;
}

nav ul{
    padding-left: 0%;
}

nav li{
    display:inline;
}

nav a, .notActiveBt{
    height:45px;
    font-size:110%;
    text-decoration:none;
    color: white;
    display:inline-block;
    border-radius: 5px;
    padding: 9px 1% 0px 1%;
}

nav a, .notActiveBt, .header-login-out a{
    transition: background 1s, color 0.5s, border-radius 1s;
    -webkit-transition: background 1s, color 0.5s, border-radius 1s;
    -moz-transition: background 1s, color 0.5s, border-radius 1s;
    -ms-transition: background 1s, color 0.5s, border-radius 1s;
    -o-transition: background 1s, color 0.5s, border-radius 1s;
}

nav a:hover{
    color:blue;
    background:white;
    border-radius: 12px;
}
.activeBt{
    color:blue;
    background:white;
    height:50px;
    padding-top:12px;
    opacity:0.85;
    filter:alpha(opasity=85);/*for IE 8 i think*/
}


/* bodycontent */
section,.section_waiting, .section_waiting_back{
    width:70%;
    min-height:500px;/*for safari*/
    min-height:calc(100vh - (70px + 50px + 50px));
    background: whitesmoke;
    float: left;
    padding:10px 1% 10px 1%;
    border-right:1px solid black;
}

.no_section{
    display:none;
}

.section_waiting_back{
    background: whitesmoke;
    position: absolute;
    opacity:0.8;
}

.section_waiting{
    background:none;
    text-align: center;
}


.section_waiting .loader_contain{
    background:rgba(245, 245, 245, 0.918);
    margin-top:15%;
    margin-left:-1%;
    /* background:white; */
    height:43px;
    display: inline-block;
    width: auto;
    animation: section_spin_contain 2s linear infinite;
}

.section_waiting .loader{
    border: 10px solid white; /* Light grey */
    border-top: 10px solid blue; /* Blue */
    border-radius: 50%;
    width: 40px;
    height: 40px;
    animation: section_spin_loader 1.5s linear infinite;
    display:block;
    float: left;
}

.section_waiting .text{
    color:blue;
    display:block;
    font-size: 16px;
    width:auto;
    margin-top: 0;
    float: left;
}

/* sidebar */
aside{
    width:30%;
    min-height:500px;/*for safari*/
    min-height:calc(100vh - (70px + 50px + 50px));
    background:whitesmoke; ;
    float: right;
    padding:10px 1% 10px 1%;
    border-left:1px solid black;
}

/* footer */
footer{
    background:blue;
    width:100%;
    height:50px;
    clear: both;
    color:white;
    text-align:center;
}

/* for the forms */
.formInputLabel{
    font-size:100%;
}

.formInput {   
  
    font-size: 100%;
    height:32px;
    width:300px;
}


/* for the wrong input during reg */
.wrongInput {   
    border:1px solid red;
    background:rgb(255, 249, 249);
}

form{
    background:#ddc8b6;
    width:330px;
    margin-top:5vh;
    padding:3vh 0 3vh 0;
    animation:form 2s;
    -webkit-animation:form 2s;
    -moz-animation:form 2s;
    -ms-animation:form 2s;
    -o-animation:form 2s;
}

form div{
    width:300px;
    padding:5px 0 5px 0;
    position:relative;
    animation:formInside 2s;
    -webkit-animation:formInside 2s;
    -moz-animation:formInside 2s;
    -ms-animation:formInside 2s;
    -o-animation:formInside 2s;
}

form input{
    font-size:100% ;
    position:relative;
    animation:formInside 2s;
    -webkit-animation:formInside 2s;
    -moz-animation:formInside 2s;
    -ms-animation:formInside 2s;
    -o-animation:formInside 2s;
}

form input[type="text"], form input[type="email"], form input[type="password"]{
    width:300px;
}
.password{
    padding-right: 30px;
}
.visibility{
    cursor: pointer;
    width:23px;
    position: relative;
    left:273px;
    top:-14px;
}

form input[type="submit"]{
    cursor: pointer;
    display:block;
    background: rgb(27, 27, 175);
    color:white;
    width:300px;
    margin: 10px 0px 0px 0px;
    border-color: rgb(27, 27, 175);
}

form textarea{
    display:block;
    width:100%;
    height:15vh;

    font-size: 100%;
}

.reg-or-log, .reg-or-log a, .reg-or-log a:visited{
    text-align: center;
    margin-top:0px;
    text-decoration: none;
    color: rgb(128, 155, 211) ;
    font-size:100%;
}

.reg-or-log a, .reg-or-log a:visited{
    color: rgb(27, 27, 175) ;
    display: inline-block;
}

.reg-or-log a:hover{
    background: rgb(27, 27, 175);
    color:white;
}

.errorMeg{
    color:red;
    background:white;
    display:inline-block;
    font-size: 90%;
    text-align:left;
}

.hide{
    display:none;
}

.meg{
    text-align: center;
    background:#ebdbce ;
    min-width: 40%;
}


